@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
}
html{
  background-color: #d8dee0;
}
.Home{
  display: flex;
  flex-direction: column;
  min-height: 80vh;
  background-image: url(/my-site/static/media/123.8c3b96df.gif);
  background-position: center;
  transform: translateY(-14px);
}

.Home h1, h3 {
  font-size: 3vh;
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.Home h1 {

}

.Home h3 {
  bottom: 11vh;
}

.Home span {
  background-color: rgba(255, 255, 255, 0.726);
}

.twilogo, .instalogo {
  height: 5vh;
  transition-duration: 0.4s;
}

.twilogo:hover, .instalogo:hover{
  transform: scale(1.2);
}

footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  background-color: #e0e0e09d;
  padding: 2vh 10vw;
}


.SetupDetails {
  color: #3b5f62;
  text-align: center;
  background-color: #d8dee0;
}

.Setupimage {
  -webkit-filter: saturate(90%);
          filter: saturate(90%);
  cursor: pointer;
  width: 100%;
  transition-duration: 0.4s;
}

.Setupimage:hover {
  -webkit-filter: saturate(100%);
          filter: saturate(100%);
  transform: matrix(1.02, 0, 0, 1.02, 0, -5);
  box-shadow:0 15px 30px #afbec2;
  transition-duration: 0.4s;
}

@media screen and (min-width: 1700px) {
  .Setupimage{
    -webkit-filter: saturate(30%);
            filter: saturate(30%);
    width: 1600px;
  }
}

.tabs {
  transition-duration: 0.5s;
}

.tabs:hover{
  transform: translateY(-5px);
  transition-duration: 0.4s;
}

.itemname {
  text-align: center;
}

.list a {
  color: #5a8e92;
  float: right;
}

.list a:hover {
  color: rgb(255, 174, 43);
}

  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-gap: 70px;
  }

  .cards {
    max-width: 1440px;
    margin:0 auto;
    margin-top: 4vh;
    margin-bottom: 10%;
  }
   
  article {
    position: relative;
  }
   
  .article-img {
    height: 200px;
    width: 100%;
    border-radius: 8px / 8px;
    object-fit: cover;
    -webkit-filter: saturate(20%);
            filter: saturate(20%);
    transition-duration: 0.4s;
  }

  .article-img:hover {
    transform: matrix(1.04, 0, 0, 1.04, 0, -5);
    box-shadow:0 15px 30px #afbec2;
    -webkit-filter: saturate(100%);
            filter: saturate(100%);
    border-radius: 0%;
    transition-duration: 0.4s;
  }
   
  .article-title {
    color: #3b5f62;
    margin: 0 auto;
    padding: 0 0 0 10px;
  }
    .hooks-main > svg {
    display: none;
  }
  
  .hooks-main > div {
    position: absolute;
    will-change: transform;
    border-radius: 50%;
    background: #ffffff;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    opacity: 0.5;
  }
  
  .hooks-main > div:nth-child(1) {
    width: 120px;
    height: 120px;
  }
  
  .hooks-main > div:nth-child(2) {
    width: 250px;
    height: 250px;
  }
  
  .hooks-main > div:nth-child(3) {
    width: 150px;
    height: 150px;
  }

  .hooks-main > div:nth-child(4) {
    width: 100px;
    height: 100px;
  }
  
  .hooks-main > div::after {
    content: '';
    position: absolute;
    top: 20px;
    left: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.8);
  }
  
  .hooks-main > div:nth-child(2)::after {
    top: 70px;
    left: 70px;
    width: 70px;
    height: 70px;
  }
  
  .hooks-main > div:nth-child(3)::after {
    top: 50px;
    left: 50px;
    width: 50px;
    height: 50px;
  }

  .hooks-main > div:nth-child(4)::after {
    top: 40px;
    left: 40px;
    width: 40px;
    height: 40px;
  }
  
  .hooks-main {
    position: fixed;
    width: 100%;
    height: 88%;
    -webkit-filter: url('#goo');
            filter: url('#goo');
    overflow: hidden;
  }

.about {
  color: #3b5f62;
  background-color: #d8dee0;
}  

dt {
    padding: 0 0 0 30px;
}

dd {   
    padding: 0 0 40px 0;
}

@media screen and (min-width: 768px) {
  .hooks-main{
    height: 96%;
  }
    dl {
        padding: 20vh 0 30vh 20%;
    }    
    dt {
        padding: 20px 20px 20px 0;
        clear: left;
        float: left;
        width: 150px;
    }
    dd {
        padding: 30px 0 10px 0;
    }
}

.contact {
  color: #3b5f62;
  margin: 0 10vw;
  width: auto;
  height: 80vh;
}

.contact h1 {
  text-align: center;
  font-size: 3.5vh;
}

.contact input[type=text],input[type=email], select, textarea {
  width: 100%;
  height: 5vh;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

.contact input[type=submit] {
  background-color:  #5f9094;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition-duration: 0.3s;
}

.contact input[type=submit]:hover {
  background-color: #86b4b68c;
  transition-duration: 0.3s;
}
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.links {
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
  margin: 0 10vw;
}

.icon {
  height: 4vh;
  -webkit-filter: opacity(70%);
          filter: opacity(70%);
  transition-duration: 0.3s;
}

.icon:hover {
  -webkit-filter: opacity(30%);
          filter: opacity(30%);
  transition-duration: 0.3s;
}

.submitbutton {
  margin-left: 50%;
  transform: translateX(-50%);
}
